// Override Button variants
// -------------------------
// Easily pump out default styles, as well as :hover, :focus, :active,
// and disabled options for all buttons
.button-variant(@color; @background; @border;) {
  .button-variant(@color; @background; @border; darken(saturate(@background, 10%), 10%); darken(saturate(@border, 10%), 10%); @color; @background)
}

// Panels
// -------------------------
.panel-variant(@border; @heading-text-color; @heading-bg-color; @heading-border;) {
  border-color: @border;
  & > .panel-heading {
    color: @heading-text-color;
    background-color: @heading-bg-color;
    border-color: @heading-border;
    + .panel-collapse .panel-body {
      border-top-color: @border;
    }

    .panel-title {
      color: @heading-text-color;
    }
  }
  & > .panel-footer {
    + .panel-collapse .panel-body {
      border-bottom-color: @border;
    }
  }
}


.button-variant(@color; @background; @border; @hoverBackground; @hoverBorder; @activeColor; @activeBackground) {
  #gradient > .vertical(@start-color: @background; @end-color: @background;);

  color: @color;

  border-left-width: 0;
  border-right-width: 0;
  border-top-width: 0;
  border-bottom-width: 2px;

  opacity: 1;

  border-color: @border;

  &:focus {

  }

  &:active,
  &.active,
  .open .dropdown-toggle& {
    border-top-width: 2px;
    border-bottom-width: 0;
    background: @activeBackground;
    .box-shadow(none);
    color: @activeColor;
  }

  &:hover {
    transition: opacity 250ms, border 250ms, color 250ms;
    color: @color;
    background: @hoverBackground;
    border-color: @hoverBorder;
  }
  &.active:hover {
    background: @activeBackground;
    color: @activeColor;
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &:active,
    &.active {
      background-color: @background;
      border-color: @border;
    }

    &.active {
      background-color: darken(saturate(@background, 50%), 50%);
    }
  }
}


// Form control focus state
//
// Generate a customized focus state and for any input with the specified color,
// which defaults to the `@input-focus-border` variable.
//
// We highly encourage you to not customize the default value, but instead use
// this to tweak colors on an as-needed basis. This aesthetic change is based on
// WebKit's default styles, but applicable to a wider range of browsers. Its
// usability and accessibility should be taken into account with any change.
//
// Example usage: change the default blue border and shadow to white for better
// contrast against a dark gray background.

.form-control-focus(@color: @input-border-focus) {
  &:focus {
    border-color: @color;
    background-image: none;

    border-left-width: 1px;
    border-right-width: 1px;

    transition: border 0ms;

    outline: 0;
    .box-shadow(none);
  }
}

// Form validation states
//
// Used in forms.less to generate the form validation CSS for warnings, errors,
// and successes.
.form-control-validation(@text-color: #555; @border-color: #ccc; @background-color: #f5f5f5) {
  // Set the border and box shadow on specific inputs to match
  .form-control {
    background-image: none;
    border-top: 1px solid @border-color;
    border-bottom: 1px solid @border-color;
    border-left: 1px solid @border-color;
    border-right: 1px solid @border-color;

    .box-shadow(none);
  }
}
// Nav
// -------------------------
.sub-nav-pushable-right (@originalLeft: 0) {
	.transition-duration(@sub-nav-duration);
	.transition-property(left);
	.is-sidebar-open & {
		left: @sub-nav-width + @originalLeft;
	}
}
