//
// Breadcrumbs
// --------------------------------------------------

.breadcrumb {
  padding: 0;
  margin-bottom: 0;
  > li {
  	font-size: 14px;
    a {
      color: @breadcrumb-active-color;
    }

    &:after {
    	&:extend([class^="icon-"]:before, .icon-arrow-right:before);
    	padding: 0 5px 0 10px;
    	font-size: 11px;
    	color: @breadcrumb-color;
    }

    &+li:before {
      content: ""; // Overrides default to remove "/"
      padding: 0;
    }

  }
  > .active {
    color: @breadcrumb-active-color;
  }
}

.breadcrumb-tiny {
	list-style: none;
	margin: 0;
	padding: 0;
	line-height: 1em;

	>li {
		display: inline-block;
		font-size: 11px;
		.lh-opacity(0.7);
		text-overflow: ellipsis;
		max-width: 110px;
		white-space: nowrap;
		overflow: hidden;
	}
	>li:after {
		padding: 0 0 0 4px;
		font-size: 8px;
		color: @breadcrumb-color;
	}
}
