.icon-circle-add:hover {
  &:before {
    &:extend([class^="icon-"]:before, .icon-circle-add-filled:before);
  }
}
.icon-circle-remove:hover {
  &:before {
    &:extend([class^="icon-"]:before, .icon-circle-remove-filled:before);
  }
}

.icon-loading {
	.lh-animation(rotation 1s infinite linear);
	// this icon has to be centered perfectly
	// or else it looks really awkward when rotationg
	height: 1em;
	line-height: 1;
}

.divider {
	display: inline-block;
	width: 9px;
	background: url(@sprite-url) no-repeat;
	background-position: -2px -37px;
	height: 8px;
	margin: 0 3px;
}
