.alert {
  position: relative;
  font-family: @font-family-sans-serif;
  font-size: @font-size-large;
  &:before {
    position: absolute;
    top: 13px;
    left: 20px;
    &:extend([class^="icon-"]:before);

  }
  padding-left: 47px;
  // TODO inform others about .alert-link
  a {
    color: @gray-lightest;
    text-decoration: underline;
  }
  a:hover {
    color: @white;
  }

  // TODO move to using .alert-dismissable
  > .close {
    color: @white;
    font-size: 16px;

    margin: 5px 7px 0;
    .lh-transition(.2s opacity);
    // I don't like this opacity mixin, but the bootstrap declaration this is overriding uses it
    .opacity(.5);
    font-weight: normal;
    text-shadow: none;
    &:hover {
      .opacity(1);
    }
  }
}

.alert-success {
  &:before {
    &:extend(.icon-check:before);
  }
}
.alert-info {
  &:before {
    &:extend(.icon-info:before);
  }
}
.alert-warning {
  &:before {
    &:extend(.icon-bolt:before);
  }
}
.alert-danger {
  &:before {
    &:extend(.icon-ban:before);
  }
}
