//
// Tables
// --------------------------------------------------

// Zebra-striping
//
// Default zebra-stripe styles (alternating gray and transparent backgrounds)

.table-striped > tbody > tr {
  > td,
  > th {
    border-top: 1px solid @gray-lightest;
    border-bottom: 1px solid @gray-lightest;
    @media (max-resolution: @projector-max-resolution) and (max-device-width: @projector-max-width) {
      border-top-color: darken(@gray-lightest, 10%);
      border-bottom-color: darken(@gray-lightest, 10%);
    }
  }
}


.table-minimal {
  thead tr th,
  tfoot tr th,
  thead tr td,
  tbody tr td,
  tfoot tr td {
    border: none;
    padding-left: 0;
  }
}



.table {
  td:not(:first-of-type), th:not(:first-of-type) {
    &.first-in-group {
      border-left: 1px solid @gray;

      &.is-first {
          border-left: 0;
      }
    }

    &.last-in-group {
      border-right: 1px solid @gray;

      &.is-last {
          border-right: 0;
      }
    }
  }

  > thead > tr > td {
      border-top: 0;

      &:not(:first-of-type).group-header {
          border-left: 1px solid @gray;
      }

      &:not(:last-of-type).group-header {
          border-right: 1px solid @gray;
      }

      &.group-header {
          text-transform: uppercase;
          font-size: 11px;
          color: @gray-dark;
      }
  }

  // Cells
  > thead,
  > tbody,
  > tfoot {
    > tr {
      > th,
      > td {
        line-height: @table-line-height;
      }
    }
  }

  > thead > tr > th {
    border-bottom: 1px solid @gray;
    border-top: 0;

    &.active {
        background-color: #eeedf2;
    }
  }

  th {
    color: @gray-dark;
    font-family: @font-family-sans-serif-bold;
    font-size: @font-size-small;

    &.left {
       text-align: left;
    }

    &.right {
       text-align: right;
    }

    &.active {
      .content {
        padding-right: 20px;
        position: relative;

        .icon-caret-up,
        .icon-caret-down {
          font-size: 10px;
          line-height: 10px;
          position: absolute;
          top: 50%;
          right: 0;
          margin-top: -5px;
        }
      }
    }
  }

  tr {
    td.word-break {
      span {
        word-break: break-all;
        max-height: 100px;
        min-width: 125px;
      }
    }
  }

  tbody tr td span {
    text-overflow: ellipsis;
    display: inline-block;
    overflow: hidden;
    vertical-align: middle;
  }

  > tbody > tr > td.active {
    background-color: @purple-lighter;
  }

  .align {
    &.middle, &.center {
      text-align: center;
    }

    &.right {
      text-align: right;
    }

    &.left {
      text-align: left;
    }

    vertical-align: middle;
  }
}

.table-body {
  width: 100%;
  float: left;
  font-size: 13px;
  overflow-x: hidden;
  overflow: visible;

  .icon-active,
  .icon-inactive {
    font-size: 17px; /* Temporary until icons get enlarged */
    position: relative;
    top: 3px; // centers the vertical alignments of these icons in the table rows
  }

  .icon-active {
    color: @brand-success;
  }

  .icon-inactive {
    color: @gray;
  }

  tr.selected {
    overflow:hidden;

    td {
      background: @table-bg-active !important;
      color: @table-bg-active-color;

      span, a, label, i {
        color:@table-bg-active-color !important;
      }
      .progress {
        background-color:@gray;
        .progress-bar {
          background-color:@table-bg-active-color;
        }
      }
      .data-vis .chart .bars rect {
        fill: @table-bg-active-color;
      }
    }
  }

  thead th {
      &.not-sortable {
          cursor: default;

          &:hover {
            background-color:transparent;
        }
    }

    &:hover {
        background-color: @gray-lightest;
    }
  }

  tbody tr {
    &:hover {
        cursor: default;

      td {
          background-color: @table-bg-hover;
      }
    }
  }
}

table.stickyfooter {
  tfoot {
    z-index: 1; /* put on top of other rows */
    top: 450px;
    position: fixed;
  }

  tfoot tr td {
    background: #ddd;
  }
}

.table-hover > tbody > tr:hover {
  > td,
  > th {
    background-color: transparent;
  }
}

.table-hover > tbody > tr:not(.no-hover):hover {
  > td,
  > th {
    background-color: @table-bg-hover;
  }
}
