//
// Navs
// --------------------------------------------------

.nav > li > a {
    font-size: 12px;

    margin: @nav-link-margin;
}

.nav.nav-pills > li > a {
	padding-right: 10px;
	padding-left: 10px;
	margin: 3px;
}

.nav.nav-tabs {
	border-bottom: none;
	border-bottom: 1px solid @gray-light;
	> li {
		margin-right:20px;
		&.active > a {
			color: @nav-tabs-active-link-color;
			border: none;
			border-bottom: 3px solid @nav-tabs-active-link-hover-border-color;
			padding: 0 0 10px 0;

			&:hover {
				cursor: pointer;
				border-color: @nav-tabs-active-link-hover-border-color;
			}
		}

		> a {
			font-size: 15px;
			color: @nav-tab-link-color;
			border: none;
			padding: 0 0 10px 0;
			margin:0;
			&:active,
			&:hover {
				border: none;
				border-bottom: 3px solid @nav-tab-link-color;
				background-color: transparent; //
			}

			&.disabled {
				color: #ccc;
				cursor: default;
			}
		}
	}
}