//
// Variables
// --------------------------------------------------


// Global values
// --------------------------------------------------
@sprite-url:   		"../../assets/img/sprite.png";

// Primary Colors
// -------------------------

@blue:                      #0d9af8; // Info
@blue-dark:                 #005cb8;

@orange:                    #f68a20; // Warning
@orange-dark:               #f26c21;
@orange-light:              #FFAA55;

@red:                       #FE6968; // Error

@maroon:                    #b41782;

@green:                     #00B777; // Success

@teal:                      #5dc3bb;

@purple:                    #6e52a2; // Primary purple
@purple-medium:             #a99ac8; // Tags
@purple-light:              #f0f0f5; // Table hover state, form focused state
@purple-lighter:            #eeedf2;
@purple-yahoo:              #400090; // Yahoo Purple


// Secondary Colors
// -------------------------
@yellow:				            #EDCC3E;
@white:                     #fff;

// Primary Grays
// -------------------------

@gray-darkest:              #424244;
@gray-darker:               #676769;
@gray-dark:                 #999999;
@gray:                      #afb0b0;
@gray-light:                #dfdfdf;
@gray-lighter:              #eaeaea;
@gray-lightest:             #f1f1f1;
@black:                     @gray-darkest;


// Brand colors
// -------------------------

@brand-primary:         @purple;
@brand-success:         @green;
@brand-warning:         @orange;
@brand-danger:          @red;
@brand-info:            @blue;


// Scaffolding
// -------------------------

@body-bg:               #f9f9fa;
@text-color:            @gray-darkest;
@nav-bg:                @gray-darkest;
@border-color:          @gray-light;
@border-color-light:    @gray-lightest;
@header-bg:             @black;
@header-border-color:   @border-color;

// Links
// -------------------------

@link-color:            #0a80e3;
@link-hover-color:      #0a80e3;

// Typography
// -------------------------

@font-face {
  font-family: "Custom Helvetica Neue Thin";
  src: url("../fonts/helveticaneue-thin.eot");
  src: url("../fonts/helveticaneue-thin.eot#iefix") format("embedded-opentype"),
  url("../fonts/helveticaneue-thin.woff") format("woff"),
  url("../fonts/helveticaneue-thin.ttf") format("truetype");
}

@font-face {
  font-family: "Custom Helvetica Neue Light";
  src: url("../fonts/helveticaneue-light.eot");
  src: url("../fonts/helveticaneue-light.eot#iefix") format("embedded-opentype"),
  url("../fonts/helveticaneue-light.woff") format("woff"),
  url("../fonts/helveticaneue-light.ttf") format("truetype");
}

@font-family-sans-serif: 'HelveticaNeue-Light', 'Helvetica 45 Light', 'HelveticaNeue Light', 'Custom Helvetica Neue Light', 'Helvetica Neue', Helvetica, Arial, sans-serif;
@font-family-sans-serif-bold: 'Helvetica Neue', Helvetica, Arial, sans-serif;
@font-family-serif:       Georgia, 'Times New Roman', Times, serif;
@font-family-monospace:   Monaco, Menlo, Consolas, 'Courier New', monospace;
@font-family-base:        @font-family-sans-serif;
@font-family-strong:  @font-family-sans-serif-bold;


//Font Sizes
@font-size-base:        14px;
@font-size-xsmall:      10px;
@font-size-small:       11px;
@font-size-mid:         12px;
@font-size-large:       17px;
@font-size-xlarge:      ceil(@font-size-base * 1.5);
@font-size-xxlarge:     ceil(@font-size-base * 1.7);
@font-size-xxxlarge:    ceil(@font-size-base * 2);

@line-height-base:        1.428571429; // 20/14
@line-height-computed:    floor(@font-size-base * @line-height-base); // ~20px

@headings-font-family: 'HelveticaNeue-Thin', 'Helvetica 35 Thin', 'HelveticaNeue Thin', 'Custom Helvetica Neue Thin', 'Helvetica Neue', Helvetica, Arial, sans-serif;
@headings-font-weight:    200;
@headings-line-height:    1.1;

// Iconography
// -------------------------

@icon-font-path:          "../fonts";
@icon-font-name:          "ads-data-icons";


// Components
// -------------------------
// Based on 14px font-size and 1.428 line-height (~20px to start)

@padding-base-vertical:          5px;
@padding-base-horizontal:        10px;

@padding-large-vertical:         10px;
@padding-large-horizontal:       16px;

@padding-small-vertical:         5px;
@padding-small-horizontal:       10px;

@line-height-large:              1.33;
@line-height-small:              1.5;

@border-radius-base:             3px;
@border-radius-large:            3px;
@border-radius-small:            3px;

@component-active-bg:            @purple-yahoo;

@caret-width-base:               4px;
@caret-width-large:              5px;

// Tables
// -------------------------

@table-cell-padding:                 9px 9px 4px 9px;
@table-condensed-cell-padding:       2px;
@table-line-height:                  1.4;

@table-bg:                           transparent; // overall background-color
@table-bg-accent:                    #fff; // for striping
@table-bg-hover:                     @gray-lighter;
@table-bg-active:                    @gray-darker;
@table-bg-active-color:              @white;

@table-border-color:                 @border-color-light; // table and cell border


// Buttons
// note: borders have been removed, not an optional part of
// the design.  it's always computed
// -------------------------

@btn-font-weight:								normal;

@btn-primary-color:							#fff;
@btn-primary-bg:								#6e54a2;
@btn-primary-border:						#5c2e8c;
@btn-primary-hover:							#5c2e8c;
@btn-primary-hover-border:			#2b2057;
@btn-primary-active-color:			@btn-primary-color;
@btn-primary-active-bg:					#2b2057;

@btn-secondary-color:						@gray-darker;
@btn-secondary-bg:							@gray-lightest;
@btn-secondary-border:					#e2e1e3;
@btn-secondary-hover:						#e2e1e3;
@btn-secondary-hover-border:		#c9ccd3;
@btn-secondary-active-color:		@btn-secondary-color;
@btn-secondary-active-bg:				@btn-secondary-hover;

@btn-default-color:							@gray-darker;
@btn-default-bg:								@white;
@btn-default-border:						@gray-light;
@btn-default-hover:							@btn-secondary-hover;
@btn-default-hover-border:			@btn-secondary-hover-border;
@btn-default-active-color:			@white;
@btn-default-active-bg:					@gray-darker;

@btn-success-color:							#fff;
@btn-success-bg:								#5dbe85;
@btn-success-border:            #17b476;
@btn-success-hover:  						#43b775;
@btn-success-hover-border:			#15a66d;
@btn-success-active-color:			@btn-success-color;
@btn-success-active-bg:					#2b2057;

@btn-warning-color:							#fff;
@btn-warning-bg:								#f58920;
@btn-warning-border:						#e27425;
@btn-warning-hover:							#f47f20;
@btn-warning-hover-border:			#e27425;
@btn-warning-active-color:			@btn-warning-color;
@btn-warning-active-bg:					#2b2057;

@btn-danger-color:							#fff;
@btn-danger-bg:									#f16767;
@btn-danger-border:							#f05050;
@btn-danger-hover:							#f05050;
@btn-danger-hover-border:				#ed1d32;
@btn-danger-active-color:				@btn-danger-color;
@btn-danger-active-bg:					#2b2057;

@btn-info-color:								#fff;
@btn-info-bg:										#4691ce;
@btn-info-border:								#397cbe;
@btn-info-hover:								#397cbe;
@btn-info-hover-border:					#415da9;
@btn-info-active-color:					@btn-info-color;
@btn-info-active-bg:						#2b2057;

@btn-link-disabled-color:				@gray-light;


// Forms
// -------------------------

@input-bg:                       @white;
@input-bg-disabled:              @gray-lighter;

@input-font-size:                @font-size-large;
@input-font-size-label:          @font-size-mid;
@input-color:                    @black;
@input-color-disabled:           @gray-light;
@input-color-locked:             @black;
@input-border:                   @border-color;
@input-border-radius:            0;
@input-border-width:             0 0 1px;
@input-border-focus:             @purple;
@input-bg-focus:                 @purple-light;

@input-color-placeholder:        @gray-light;

@input-height-base:              (@line-height-computed + (@padding-base-vertical * 2) + 2);
@input-height-large:             (floor(@font-size-large * @line-height-large) + (@padding-large-vertical * 2) + 2);
@input-height-small:             (floor(@font-size-small * @line-height-small) + (@padding-small-vertical * 2) + 2);

@legend-color:                   @gray-dark;
@legend-border-color:            @border-color-light;

@input-group-addon-bg:           @gray-lighter;
@input-group-addon-border-color: @input-border;


// Dropdowns
// -------------------------

@dropdown-bg:											@gray-darker;
@dropdown-border:									@gray-darker;
@dropdown-border-radius:					0;
@dropdown-margin-top:							0;
@dropdown-fallback-border:				#ccc;
@dropdown-divider-bg:							@gray-darkest;

@dropdown-link-active-color:			@white;
@dropdown-link-active-bg:					@gray-dark;
@dropdown-link-padding:						4px 12px;
@dropdown-link-color:							@white;
@dropdown-link-hover-color:				@white;
@dropdown-link-hover-bg:					@purple;
@dropdown-link-disabled-color:		@gray-light;

@dropdown-header-color:						@gray-dark;
@dropdown-caret-color:						#000;


// COMPONENT VARIABLES
// --------------------------------------------------


// Z-index master list
// -------------------------
// Used for a bird's eye view of components dependent on the z-axis
// Try to avoid customizing these :)

@zindex-navbar:            1000;
@zindex-dropdown:          100;
@zindex-popover:           1010;
@zindex-tooltip:           1030;
@zindex-tooltip-modal:     1060;
@zindex-navbar-fixed:      1030;
@zindex-modal-background:  1040;
@zindex-modal:             1050;

// Media queries breakpoints
// --------------------------------------------------

// Extra small screen / phone
@screen-xs:                  480px;
@screen-phone:               @screen-xs;

// Small screen / tablet
@screen-sm:                  768px;
@screen-tablet:              @screen-sm;

// Medium screen / desktop
@screen-md:                  992px;
@screen-desktop:             @screen-md;

// Large screen / wide desktop
@screen-lg:                  1200px;
@screen-lg-desktop:          @screen-lg;

// So media queries don't overlap when required, provide a maximum
@screen-xs-max:              (@screen-sm - 1);
@screen-sm-max:              (@screen-md - 1);
@screen-md-max:              (@screen-lg - 1);


// Grid system
// --------------------------------------------------

// Number of columns in the grid system
@grid-columns:              12;
// Padding, to be divided by two and applied to the left and right of all columns
@grid-gutter-width:         30px;
// Point at which the navbar stops collapsing
@grid-float-breakpoint:     @screen-tablet;


// Navbar
// -------------------------

// Basics of a navbar
@navbar-height:                    50px;
@navbar-margin-bottom:             @line-height-computed;
@navbar-default-color:             #777;
@navbar-default-bg:                @white;
@navbar-default-border:            #cac6c6;
@navbar-border-radius:             0;
@navbar-padding-horizontal:        floor(@grid-gutter-width / 2);
@navbar-padding-vertical:          ((@navbar-height - @line-height-computed) / 2);

// Navbar links
@navbar-default-link-color:                #777;
@navbar-default-link-hover-color:          #333;
@navbar-default-link-hover-bg:             transparent;
@navbar-default-link-active-color:         @brand-primary;
@navbar-default-link-active-bg:            transparent;
@navbar-default-link-active-border:        @brand-primary;
@navbar-default-link-disabled-color:       #ccc;
@navbar-default-link-disabled-bg:          transparent;

// Navbar brand label
@navbar-default-brand-color:               @navbar-default-link-color;
@navbar-default-brand-hover-color:         darken(@navbar-default-link-color, 10%);
@navbar-default-brand-hover-bg:            transparent;

// Navbar toggle
@navbar-default-toggle-hover-bg:           #ddd;
@navbar-default-toggle-icon-bar-bg:        #ccc;
@navbar-default-toggle-border-color:       @border-color;


// Inverted navbar
//
// Reset inverted navbar basics
@navbar-inverse-color:                      #efeeec;
@navbar-inverse-bg:                         @header-bg;
@navbar-inverse-border:                     @header-border-color;

// Inverted navbar links
@navbar-inverse-link-color:                 #FDFDFB;
@navbar-inverse-link-hover-color:           #fff;
@navbar-inverse-link-hover-bg:              transparent;
@navbar-inverse-link-active-color:          @navbar-inverse-link-hover-color;
@navbar-inverse-link-active-bg:             transparent;
@navbar-inverse-link-active-border:         @navbar-inverse-link-color;
@navbar-inverse-link-disabled-color:        #444;
@navbar-inverse-link-disabled-bg:           transparent;


// Inverted navbar brand label
@navbar-inverse-brand-color:                @navbar-inverse-link-color;
@navbar-inverse-brand-hover-color:          #fff;
@navbar-inverse-brand-hover-bg:             transparent;

// Inverted navbar search
// Normal navbar needs no special styles or vars
@navbar-inverse-search-bg:                  lighten(@navbar-inverse-bg, 25%);
@navbar-inverse-search-bg-focus:            #fff;
@navbar-inverse-search-border:              @navbar-inverse-bg;
@navbar-inverse-search-placeholder-color:   #ccc;

// Inverted navbar toggle
@navbar-inverse-toggle-hover-bg:            #333;
@navbar-inverse-toggle-icon-bar-bg:         #fff;
@navbar-inverse-toggle-border-color:        #333;


// Navs
// -------------------------

@nav-link-padding:                          10px 0px;
@nav-link-margin:                           0px 20px;
@nav-link-hover-bg:                         @gray-lighter;

@nav-disabled-link-color:                   @gray-light;
@nav-disabled-link-hover-color:             @gray-light;

@nav-open-link-hover-color:                 #fff;
@nav-open-caret-border-color:               #fff;

// Tabs
@nav-tabs-border-color:                     @border-color;
@nav-tab-link-color: 					              @gray-dark;
@nav-tabs-link-hover-border-color:          @gray-dark;

@nav-tabs-active-link-color: 			          @brand-primary;
@nav-tabs-active-link-hover-bg:             transparent;
@nav-tabs-active-link-hover-color:          @brand-primary;
@nav-tabs-active-link-hover-border-color:   @brand-primary;

@nav-tabs-justified-link-border-color:            @border-color;
@nav-tabs-justified-active-link-border-color:     @body-bg;

// Pills
@nav-pills-active-link-hover-bg:            @component-active-bg;
@nav-pills-active-link-hover-color:         #fff;


// Pagination
// -------------------------

@pagination-bg:                        #fff;
@pagination-border:                    #ddd;

@pagination-hover-bg:                  @gray-lighter;

@pagination-active-bg:                 @brand-primary;
@pagination-active-color:              #fff;

@pagination-disabled-color:            @gray-light;


// Pager
// -------------------------

@pager-border-radius:                  15px;
@pager-disabled-color:                 @gray-light;


// Jumbotron
// -------------------------

@jumbotron-padding:              20px;
@jumbotron-color:                #fff;
@jumbotron-bg:                   @brand-success;
@jumbrotron-border-radius:       0px;
@jumbotron-heading-color:        inherit;


// Form states and alerts
// -------------------------

@state-warning-text:             @brand-warning;
@state-warning-bg:               #ffd1af;
@state-warning-border:           @brand-warning;

@state-danger-text:              @brand-danger;
@state-danger-bg:                #fdf1f1;
@state-danger-border:            @brand-danger;

@state-success-text:             @brand-success;
@state-success-bg:               #dbf4eA;
@state-success-border:           @brand-success;

@state-info-text:                @brand-info;
@state-info-bg:                  #d0ebfc;
@state-info-border:              @brand-info;


// Tooltips
// -------------------------
@tooltip-max-width:           200px;
@tooltip-color:               #fff;
@tooltip-bg:                  @dropdown-bg;

@tooltip-arrow-width:         5px;
@tooltip-arrow-color:         @tooltip-bg;


// Popovers
// -------------------------
@popover-bg:                          #fff;
@popover-max-width:                   276px;
@popover-border-color:                @border-color-light;
@popover-fallback-border-color:       @border-color-light;

@popover-title-bg:                    darken(@popover-bg, 3%);

@popover-arrow-width:                 10px;
@popover-arrow-color:                 #fff;

@popover-arrow-outer-width:           (@popover-arrow-width + 1);
@popover-arrow-outer-color:           rgba(0,0,0,.25);
@popover-arrow-outer-fallback-color:  #999;


// Labels
// -------------------------

@label-default-bg:            @gray-light;
@label-primary-bg:            @brand-primary;
@label-success-bg:            @brand-success;
@label-info-bg:               @brand-info;
@label-warning-bg:            @brand-warning;
@label-danger-bg:             @brand-danger;
@label-padding:               0.35em .6em .3em;
@label-color:                 #fff;
@label-link-hover-color:      #fff;


// Forms
// -------------------------
@form-label-color: 			  @gray-darker;


// Modals
// -------------------------
@modal-inner-padding:         20px;

@modal-title-padding:         20px;
@modal-title-line-height:     @line-height-base;

@modal-content-bg:                             #fff;
@modal-content-border-color:                   @border-color-light;
@modal-content-fallback-border-color:          @border-color-light;

@modal-backdrop-bg:           #000;
@modal-header-border-color:   @border-color;
@modal-footer-border-color:   @modal-header-border-color;


// Alerts
// -------------------------
@alert-padding:               10px;
@alert-border-radius:         0;
@alert-link-font-weight:      bold;

@alert-success-bg:            @green;
@alert-success-text:          @white;

@alert-info-bg:               @blue;
@alert-info-text:             @white;

@alert-warning-bg:            @orange;
@alert-warning-text:          @white;

@alert-danger-bg:             @red;
@alert-danger-text:           @white;


// Progress bars
// -------------------------
@progress-bg:                 #f5f5f5;
@progress-bar-color:          #fff;

@progress-bar-bg:             @brand-primary;
@progress-bar-success-bg:     @brand-success;
@progress-bar-warning-bg:     @brand-warning;
@progress-bar-danger-bg:      @brand-danger;
@progress-bar-info-bg:        @brand-info;


// List group
// -------------------------
@list-group-bg:               #fff;
@list-group-border:           #ddd;
@list-group-border-radius:    @border-radius-base;

@list-group-hover-bg:         #f5f5f5;
@list-group-active-color:     #fff;
@list-group-active-bg:        @component-active-bg;
@list-group-active-border:    @list-group-active-bg;

@list-group-link-color:          #555;
@list-group-link-heading-color:  #333;


// Panels
// -------------------------
@panel-bg:                    #fff;
@panel-inner-border:          #ddd;
@panel-border-radius:         @border-radius-base;
@panel-footer-bg:             #f5f5f5;

@panel-default-text:          @gray-dark;
@panel-default-border:        #ddd;
@panel-default-heading-bg:    #f5f5f5;

@panel-primary-text:          #fff;
@panel-primary-border:        @brand-primary;
@panel-primary-heading-bg:    @brand-primary;

@panel-success-text:          @state-success-text;
@panel-success-border:        @state-success-border;
@panel-success-heading-bg:    @state-success-bg;

@panel-warning-text:          @state-warning-text;
@panel-warning-border:        @state-warning-border;
@panel-warning-heading-bg:    @state-warning-bg;

@panel-danger-text:           @state-danger-text;
@panel-danger-border:         @state-danger-border;
@panel-danger-heading-bg:     @state-danger-bg;

@panel-info-text:             @state-info-text;
@panel-info-border:           @state-info-border;
@panel-info-heading-bg:       @state-info-bg;


// Thumbnails
// -------------------------
@thumbnail-padding:           4px;
@thumbnail-bg:                @body-bg;
@thumbnail-border:            #ddd;
@thumbnail-border-radius:     @border-radius-base;

@thumbnail-caption-color:     @text-color;
@thumbnail-caption-padding:   9px;


// Wells
// -------------------------
@well-bg:                     #f5f5f5;


// Badges
// -------------------------
@badge-color:                 @gray-darker;
@badge-link-hover-color:      #fff;
@badge-bg:                    @gray-light;

@badge-active-color:          @link-color;
@badge-active-bg:             #fff;

@badge-font-weight:           bold;
@badge-line-height:           1;
@badge-border-radius:         10px;


// Breadcrumbs
// -------------------------
@breadcrumb-bg:               transparent;
@breadcrumb-color:            @gray-light;
@breadcrumb-active-color:     @gray-darker;


// Carousel
// ------------------------

@carousel-text-shadow:                        0 1px 2px rgba(0,0,0,.6);

@carousel-control-color:                      #fff;
@carousel-control-width:                      15%;
@carousel-control-opacity:                    .5;
@carousel-control-font-size:                  20px;

@carousel-indicator-active-bg:                #fff;
@carousel-indicator-border-color:             #fff;

@carousel-caption-color:                      #fff;


// Close
// ------------------------
@close-color:                 #000;
@close-font-weight:           bold;
@close-text-shadow:           0 1px 0 #fff;


// Code
// ------------------------
@code-color:                  #c7254e;
@code-bg:                     #f9f2f4;

@pre-bg:                      @gray-lightest;
@pre-color:                   @gray-dark;
@pre-border-color:            @border-color;
@pre-scrollable-max-height:   340px;

// Type
// ------------------------
@text-muted:                  @gray;
@abbr-border-color:           @border-color;
@headings-small-color:        @gray-dark;
@blockquote-small-color:      @gray-dark;
@blockquote-border-color:     @gray;
@page-header-border-color:    @border-color;

// Miscellaneous
// -------------------------

// Hr border color
@hr-border:                   @border-color;

// Horizontal forms & lists
@component-offset-horizontal: 180px;


// Projector settings
@projector-max-resolution: 1dppx;
@projector-max-width: 1280px;

// Container sizes
// --------------------------------------------------

// Small screen / tablet
@container-tablet:            ((720px + @grid-gutter-width));

// Medium screen / desktop
@container-desktop:           ((940px + @grid-gutter-width));

// Large screen / wide desktop
@container-lg-desktop:        ((1140px + @grid-gutter-width));


@form-footer-bg:   none;

// Nav
// -------------------------
@nav-primary-width: 74px;
@sub-nav-width: 200px;
@sub-nav-duration: .2s;

// Header
// // -------------------------
@main-header-margin-bottom: 15px;
@main-header-height: 66px;
