//
// Typography
// --------------------------------------------------

// Headers
// ------------------------
h1, .h1 { font-size: 28px; }
h2, .h2 { font-size: 24px; }
h3, .h3 { font-size: 21px; }
h4, .h4 { font-size: 18px; }
h5, .h5 { font-size: 15px; text-transform: uppercase; }

// todo:
h1 small, .h1 small { font-size: ceil(@font-size-base * 1.70); } // ~24px
h2 small, .h2 small { font-size: ceil(@font-size-base * 1.25); } // ~18px
h3 small, .h3 small,
h4 small, .h4 small { font-size: @font-size-base; }

h1, h2, h3, h4, 
.h1, .h2, .h3, .h4 {
  color: @gray-darkest;
  font-family: @headings-font-family;
  font-weight: @headings-font-weight;

  small {
    font-family: @font-family-sans-serif-bold;
    line-height: 1;
    color: @headings-small-color;
  }
}

a,
a:hover,
a:active,
a:focus {
  outline: 0;
  cursor: pointer;
}

// TODO see about converting dsp to use .page-header
// since that isn't totally custom/made up
.page-title {
  .lh-calc(max-width, ~'100% - 100px');
  margin-bottom: 15px;

  .title-heading {
    margin: 3px 0 7px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    line-height: 1.2em;
  }

  .title-properties {
    font-family: @font-family-sans-serif-bold;
    .id,
    .type,
    .toggle-panel {
      color: @gray;
      font-size: @font-size-mid;
      padding: 4px 10px;
      float: left;
      line-height: 1em;
    }

    .type {
      background: @teal;
      color: @white;
      text-transform: uppercase;
    }

    .id {
      padding: 5px;
    }

    a.toggle-panel {
      line-height: 1.25em;
      font-size: @font-size-large;
      padding: 0;
      margin: 2px 0 0 3px;
      &:hover {
        text-decoration: none;
      }
    }
  }
}
a.toggle-panel {
  color: @gray;
}
.settings-panel {
  .is-opened {
    border-top: 1px solid @border-color;
  }
}

// Metrics

.val {
  font-family: @headings-font-family;
}
