@leftWidth: 20px;

global-search {
    display: block;
}

.search-container {
    position: relative;
    margin-top: 15px;
    margin-bottom: 15px;

    .loader-dots-small {
        position: absolute;
        right: 15px;
        top: 50%;
        margin-top: -3px;

        .dot {
            background-color: @gray;
        }
    }

    .icon-circle-remove-filled {
        cursor: pointer;
    }

    .icon-search,
    .icon-circle-remove-filled {
        font-size: 17px;
        position: absolute;
        right: 22px;
        top: 6px;
    }

    input {
        border: 0;
        border-bottom: 1px solid @gray;
        font-size: 17px;
        padding-right: 45px;
        .lh-border-radius(0);
        .lh-box-shadow(none);
    }

    ul {
        .lh-box-shadow(2px 2px 3px rgba(136, 136, 136, 0.34));
		list-style: none;
		padding: 0;
		margin: 0;
		position: absolute;
		left: 0;
		background: @white;
    width: 100%;
		border-bottom-left-radius: 3px;
		border-bottom-right-radius: 3px;

		li {
			font-size: 11px;
			padding: 5px;
		}
	}

    .search-result-container {
        position: absolute;
        z-index: 100;
        display: block;
        right: auto;
        .lh-calc(width, ~'100% - 30px');
        padding: 8px 0;
        background-color: #3C373F;
        max-height:600px;
        overflow: auto;
        border: 1px solid #ccc;
        border: 1px solid rgba(0, 0, 0, 0.2);
        .lh-box-shadow(0 5px 10px rgba(0,0,0,.2));

        .heading, .header {
            color: @white;
        }

        .heading {
            padding-left: @leftWidth;
            padding-top: 5px;
            padding-bottom: 5px;
            font-size: 12px;
            text-transform: uppercase;
        }

        .header {
            padding-left: @leftWidth;
            font-weight: bold;
            margin-top: 10px;
            background-color: #3C373F;
        }


        .search-results {
            a {
                display: block;
                white-space: nowrap;
                cursor: pointer;
                padding: 0 @leftWidth;
                line-height: 24px;
                font-weight:lighter;
                color: @gray-dark;
                margin: 0;
                text-overflow: ellipsis;
                overflow-x: hidden;

                .result,
                .entity-id {
                    line-height: 24px;
                    vertical-align: middle;
                }

                .result {
                    .pull-left();
                    .lh-calc(max-width, ~'100% - 60px');

                    overflow: hidden;
                    text-overflow: ellipsis;
                }

                .entity-id {
                    .pull-right();

                    color: @gray-darker;
                }

                .highlight {
                    color: @gray-light;
                    font-weight: bold;
                }

                &.is-active {
                    background-color: @purple;
                    text-decoration: none;

                    .result,
                    .entity-id {
                        color: @white;
                    }
                }
            }
        }
    }
}