//
// Primary Header
// --------------------------------------------------

header,
.header {
	background-color: @header-bg;
	border-bottom: 0;
}

#main-header {
	position: fixed;
	top: 0;
	z-index:1001;
	left: @nav-primary-width;
	.lh-calc(width, ~'100% - '@nav-primary-width);
	min-height: @main-header-height;
	margin-bottom: @main-header-margin-bottom;
	.sub-nav-pushable-right(@nav-primary-width);

	.logo {
	    display: inline-block;
	    margin-top: 17px;
	    img {
	      width: 284px;
	      height: 28px;
	    }
	}

	&.navbar-centered-menu .navbar-header {
		left: auto;
	}

	.account {
	    top: auto; // this is an override
	    right: auto; // this is an override
	    position: relative; // this is an override, but also necessary
	    text-align: right;

	    .name {
	      margin-top: 0; // to override the existing -16px
	    }

	    .btn {
	      color: @text-color;
	      .lh-border-radius(0);
	      &:hover {
	        background: @gray-lighter;
	      }
	    }
	}

	.account-dropdown {
	    display: inline-block;
	    margin-right: -15px;
	    .btn {
	      text-align: left;
	      padding: 13px 10px;
	    }
	    .icon-profile {
	      font-size: 24px;
	      vertical-align: top;
	    }
	    .user-role {
	      font-size: @font-size-small;
	    }

	    .profile-image {
	      display: inline-block;
	      img {
	        vertical-align: baseline;
	        width: 30px;
	        height: 30px;
	      }
	    }
	    .user-info {
	      margin: 0 0 0 5px;
	      display: inline-block;
	    }
  	}
  	.dropdown-menu {
    	text-align: left;
  	}

	padding: 0 15px;

	&.navbar-default {
		border-bottom: 1px solid @gray-light;
	}

	.navbar-logo {
    padding-top: 17px;
		padding-right: 0;

		img {
			width: 284px;
			height: 28px;
		}
	}
	.alert {
    margin: 0 -17px 0;
    &:first-of-type {
      margin-top: 10px;
    }
	}
	&.has-notifications {
		border-bottom: 0;
	}
}

.is-sidebar-open #main-header{
  .lh-calc(width, ~'100% - 274px');
}
.universal-header {
	.breadcrumb-item {
		line-height: 11px;
		vertical-align: top;
	}

	&.sticky {
		.lh-box-shadow(0 1px 1px 0 #CFCFCF);
		top: 0;
		z-index: 9999;
		background: @body-bg;
		padding: 10px 0;
	}

	h1 {
		margin: 0 0 10px 0;
		display: block;
		small {
			font-size: 14px;
		}
	}
}

.modal-open {
  overflow: hidden;

  body&,
  .navbar-fixed-top,
  .navbar-fixed-bottom {
    margin-right: 0;
  }
}
