.tooltip {
	.tooltip-inner {
		padding: 8px;
		border-radius: 0;
		text-align: left;

		.tooltip-header {
			font-size: @font-size-base;
		}
	}

	&.danger {
		@tooltip-arrow-color: @red;
		opacity: 1;

		&.top .tooltip-arrow,
		&.top-left .tooltip-arrow,
		&.top-right .tooltip-arrow {
			border-top-color: @tooltip-arrow-color;
		}

		&.right .tooltip-arrow {
			border-right-color: @tooltip-arrow-color;
		}

		&.left .tooltip-arrow {
			border-left-color: @tooltip-arrow-color;
		}

		&.bottom .tooltip-arrow,
		&.bottom-left .tooltip-arrow,
		&.bottom-right .tooltip-arrow {
			border-bottom-color: @tooltip-arrow-color;
		}

		.tooltip-inner {
			text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.34);
			background: @tooltip-arrow-color;
			color: lighten(@red, 50%);
		}
	}

  // TODO verify the code below deserves to exist
  &.dark {
		.tooltip-inner {
			.lh-border-radius(3px);
			background: #2b2a29;
			color: @white;
		}

		&.top .tooltip-arrow {
			border-top-color: #2b2a29;
		}

		&.bottom .tooltip-arrow {
			border-bottom-color: #2b2a29;
		}

		&.left .tooltip-arrow {
			border-left-color: #2b2a29;
		}

		&.right .tooltip-arrow {
			border-right-color: #2b2a29;
		}
	}

	&.top {
    margin-top: 4px;
  }

  &.tooltip.arrow-left {
    &:extend(.tooltip.right all);
  }

  &.tooltip.arrow-right {
    &:extend(.tooltip.left all);
  }

  &.tooltip.arrow-top {
    &:extend(.tooltip.bottom all);
  }

  &.tooltip.arrow-bottom {
    &:extend(.tooltip.top all);
  }

  &.top .tooltip-arrow {
		bottom: 1px;
	}
}

.modal-dialog .tooltip {
	z-index: @zindex-tooltip-modal;
}