.nav-primary {
  background-color: @nav-bg;
  position: fixed;
  top: 0;
  left: 0;
  width: @nav-primary-width;
  height: 100%;
  z-index: 20;
  padding-top: 80px;

  ul {
    list-style: none;
    margin: 0;
    padding: 0;

    li {
      padding: 0;
      text-align: center;
      margin-bottom: 24px;
      border-bottom: @black;

      a {
        color: @gray-light;
        font-size: 11px;
        display: block;

        [class*="icon-"] {
          .lh-transition(opacity 400ms);
          font-size: 28px;
          line-height: 34px;
          display: block;
          .lh-opacity(0.5);
        }

        span {
          .lh-transition(opacity 400ms);
          display: block;
          .lh-opacity(0.5);
        }

        &:hover,
        &:focus,
        &:active {
          text-decoration: none;
          cursor: pointer;

          [class*="icon-"],
          span {
            .lh-opacity(1.0);
          }
        }
      }

      a.active {
        [class*="icon-"],
        span {
          .lh-opacity(1.0);
        }
      }
    }
  }
}

.nav-primary.sub-nav {
  z-index: 1;
  .is-sidebar-open & {
    left: 0;
  }

  left: (-1 * @sub-nav-width);
  .lh-transition(left @sub-nav-duration);
  width: @sub-nav-width;
  margin-left: @nav-primary-width;
  background-color:@purple;

  ul {
    color: white;

    li {
      text-align: left;
      margin-bottom: 5px;
      :hover {
        background-color: rgba(255, 255, 255, 0.2);
      }

      a {
        font-size: 14px;
        padding-left: 25px;
        padding-top: 5px;
        padding-bottom: 5px;
        font-weight: 200;
      }
    }

  }
}

.nav-primary {
  .icon-bar {
    .lh-calc(height, ~'100% - 2em');
  }

  .build-tooltip {
    font-size: 11px;

    &:hover {
      cursor: default;
    }

    & + .tooltip {
    width: 100px;
    }
  }

  .sidebar-link:after {
    .transition-duration((@sub-nav-duration / 2));
    .transition-delay((@sub-nav-duration / 2));
    .transition-property(~"right opacity");
    .opacity(0);

    content: '';
    width: 0;
    height: 0;
    right: -8px;
    position: absolute;
    margin-top: -33px;
    border-style: solid;
    border-width: 7px 8px 7px 0;
    border-color: transparent @purple transparent transparent;
  }

  .active-side-panel:after {
    right: 0;
    .opacity(1);
  }
}