.select2-container {
  margin: 0;
  position: relative;
  display: inline-block;
  vertical-align: middle;

  .select2-choice {
    display: block;
    height: 26px;
    padding: 0 0 0 8px;
    overflow: hidden;
    position: relative;

    border: 1px solid #aaa;
    white-space: nowrap;
    line-height: 26px;
    color: @gray-dark;
    text-decoration: none;

    background-clip: padding-box;

    .lh-user-select(none);

    > .select2-chosen {
      margin-right: 26px;
      display: block;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    abbr {
      display: none;
      width: 12px;
      height: 12px;
      position: absolute;
      right: 24px;
      top: 8px;

      font-size: 1px;
      text-decoration: none;

      border: 0;
      cursor: pointer;
      outline: 0;
    }

  }
}

.select2-container .select2-choice abbr:hover {
  cursor: pointer;
}

.select2-drop-mask {
  border: 0;
  margin: 0;
  padding: 0;
  position: fixed;
  left: 0;
  top: 0;
  min-height: 100%;
  min-width: 100%;
  height: auto;
  width: auto;
  opacity: 0;
  z-index: 9998;
}

.select2-drop {
  width: 100%;
  margin-top: -1px;
  position: absolute;
  z-index: 9999;
  top: 100%;

  background: #fff;
  color: #000;
  border: 1px solid #aaa;
  border-top: 0;
}

.select2-drop-auto-width {
  border-top: 1px solid #aaa;
  width: auto;
}

.select2-drop-auto-width .select2-search {
  padding-top: 4px;
}

.select2-drop.select2-drop-above {
  margin-top: 1px;
  border-top: 1px solid #aaa;
  border-bottom: 0;
}

.select2-drop-active {
  // border: 1px solid #5897fb;
  border-top: none;
}

.select2-drop.select2-drop-above.select2-drop-active {
  // border-top: 1px solid #5897fb;
}

.select2-container .select2-choice .select2-arrow {
  display: inline-block;
  width: 18px;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;

  border-left: 1px solid #aaa;

  background-clip: padding-box;
}

.select2-container .select2-choice .select2-arrow b {
  display: block;
  width: 100%;
  height: 100%;
}

.select2-search {
}

.select2-search input {
  width: 100%;
  height: auto !important;
  min-height: 26px;
  padding: 4px 20px 4px 5px;
  margin: 0;

  outline: 0;
  font-size: 1em;

  border: 1px solid #aaa;
}

.select2-drop.select2-drop-above .select2-search input {
  margin-top: 4px;
}

.select2-container-active .select2-choice,
.select2-container-active .select2-choices {
  // border: 1px solid deepPink; //#5897fb
  // outline: none;
}

.select2-dropdown-open .select2-choice {
  // border-bottom-color: transparent;

  // border-bottom-left-radius: 0;
  // border-bottom-right-radius: 0;
}

.select2-dropdown-open.select2-drop-above .select2-choice,
.select2-dropdown-open.select2-drop-above .select2-choices {
  // border: 1px solid salmon; // #5897fb
  // border-top-color: transparent;
}

.select2-dropdown-open .select2-choice .select2-arrow {
  background: transparent;
  border-left: none;
  filter: none;
}

/* results */
.select2-results {
  max-height: 200px;
  padding: 0 0 0 4px;
  margin: 4px 4px 4px 0;
  position: relative;
  overflow-x: hidden;
  overflow-y: auto;
}

.select2-results ul.select2-result-sub {
  margin: 0;
  padding-left: 0;
}

.select2-results ul.select2-result-sub > li .select2-result-label { padding-left: 20px }
.select2-results ul.select2-result-sub ul.select2-result-sub > li .select2-result-label { padding-left: 40px }
.select2-results ul.select2-result-sub ul.select2-result-sub ul.select2-result-sub > li .select2-result-label { padding-left: 60px }
.select2-results ul.select2-result-sub ul.select2-result-sub ul.select2-result-sub ul.select2-result-sub > li .select2-result-label { padding-left: 80px }
.select2-results ul.select2-result-sub ul.select2-result-sub ul.select2-result-sub ul.select2-result-sub ul.select2-result-sub > li .select2-result-label { padding-left: 100px }
.select2-results ul.select2-result-sub ul.select2-result-sub ul.select2-result-sub ul.select2-result-sub ul.select2-result-sub ul.select2-result-sub > li .select2-result-label { padding-left: 110px }
.select2-results ul.select2-result-sub ul.select2-result-sub ul.select2-result-sub ul.select2-result-sub ul.select2-result-sub ul.select2-result-sub ul.select2-result-sub > li .select2-result-label { padding-left: 120px }

.select2-results li {
  list-style: none;
  display: list-item;
}

.select2-results li.select2-result-with-children > .select2-result-label {
  font-weight: bold;
}

.select2-results .select2-result-label {
  padding: 3px 7px 4px;
  margin: 0;
  cursor: pointer;

  min-height: 1em;

  .lh-user-select(none);
}

.select2-results .select2-highlighted {
  color: #fff;
}

.select2-results li em {
  font-style: normal;
}

.select2-results .select2-highlighted em {
}

.select2-results .select2-highlighted ul {
  background: #fff;
  color: #000;
}


.select2-results .select2-no-results,
.select2-results .select2-searching,
.select2-results .select2-selection-limit {
  display: list-item;
}

/*
disabled look for disabled choices in the results dropdown
*/
.select2-results .select2-disabled.select2-highlighted {
  color: #666;
  display: list-item;
  cursor: default;
}
.select2-results .select2-disabled {
  display: list-item;
  cursor: default;
}

.select2-results .select2-selected {
  display: none;
}

.select2-more-results {
  display: list-item;
}

/* disabled styles */

.select2-container.select2-container-disabled .select2-choice {
  // border: 1px solid yellow;  //#ddd
  cursor: default;
}

.select2-container.select2-container-disabled .select2-choice .select2-arrow {
  border-left: 0;
}

.select2-container.select2-container-disabled .select2-choice abbr {
  display: none;
}


/* multiselect */

.select2-container-multi .select2-choices {
  height: auto !important;
  height: 1%;
  margin: 0;
  padding: 0;
  position: relative;

  border: 1px solid orange; //#aaa
  cursor: text;
  overflow: hidden;
}

.select2-locked {
  padding: 3px 5px 3px 5px !important;
}

.select2-container-multi .select2-choices {
  min-height: 26px;
}

.select2-container-multi .select2-choices li {
  float: left;
  list-style: none;
}
.select2-container-multi .select2-choices .select2-search-field {
  margin: 0;
  padding: 0;
  white-space: nowrap;
}

.select2-container-multi .select2-choices .select2-search-field input {
  padding: 5px;
  margin: 1px 0;
  font-size: 100%;
  color: @gray-dark;
  outline: 0;
  border: 0;
}

.select2-results .select2-default {
  color: #999;
}

.select2-container-multi .select2-choices .select2-search-choice {
  padding: 3px 5px 3px 18px;
  margin: 3px 0 3px 5px;
  position: relative;

  line-height: 13px;
  color: #333;
  cursor: default;
  border: 1px solid #aaaaaa;

  background-clip: padding-box;

  .lh-user-select(none);
}
.select2-container-multi .select2-choices .select2-search-choice .select2-chosen {
  cursor: default;
}

.select2-search-choice-close {
  display: block;
  width: 12px;
  height: 13px;
  position: absolute;
  right: 3px;
  top: 4px;

  font-size: 1px;
  outline: none;
}

.select2-container-multi .select2-search-choice-close {
  left: 3px;
}

/* disabled styles */
.select2-container-multi.select2-container-disabled .select2-choices {
  background-color: #f4f4f4;
  border: 1px solid #ddd;
  cursor: default;
}

.select2-container-multi.select2-container-disabled .select2-choices .select2-search-choice {
  padding: 3px 5px 3px 5px;
  border: 1px solid #ddd;
}

.select2-container-multi.select2-container-disabled .select2-choices .select2-search-choice .select2-search-choice-close {  display: none;
  background: none;
}
/* end multiselect */

.select2-result-selectable .select2-match,
.select2-result-unselectable .select2-match {
  text-decoration: underline;
}

.select2-offscreen, .select2-offscreen:focus {
  clip: rect(0 0 0 0) !important;
  width: 1px !important;
  height: 1px !important;
  border: 0 !important;
  margin: 0 !important;
  padding: 0 !important;
  overflow: hidden !important;
  position: absolute !important;
  outline: 0 !important;
  left: 0px !important;
  top: 0px !important;
}

.select2-display-none {
  display: none;
}

.select2-measure-scrollbar {
  position: absolute;
  top: -10000px;
  left: -10000px;
  width: 100px;
  height: 100px;
  overflow: scroll;
}

.adbs-select2-drop {
  background: #3C373F;
  color: @gray-dark;

  &.select2-drop-active {
    border: 0;
  }
  .select2-results {
    margin: 4px 0;
    padding: 0;

    .select2-highlighted {
      background: @purple;
      color: @white;
    }
    .select2-no-results,
    .select2-searching,
    .select2-selection-limit {
      background: none;
      padding: 11px;
    }
  }

  .select2-result-label {
    padding: 3px 7px 4px 11px;
  }

  .select2-search {
    padding: 4px 10px;
    input {
      background: transparent;
      border: 0;
      border-bottom: 1px solid @gray;
      color: @white;
      padding: 5px 0;
      position: relative;
    }
  }
  .select2-more-results.select2-active {
    padding: 11px;
  }
}

.select2-container-multi .select2-choices .select2-search-field input.select2-active {
  background: transparent;
}

.adbs-select2-container {
  display: block;

  &.select2-container-multi .select2-choices {
    .form-jumbo .form-control;
    padding: 23px 10px 3px;
    .select2-search-field input {
      padding-left: 0;
    }
    .select2-search-choice {
      margin-left: 0;
      margin-right: 5px;
      padding-right: 5px;
      margin-top: 5px;
      .btn;
      .btn-primary;
      padding: 2px 6px;
      padding-bottom: 6px;
      cursor: default;
      background: fade(#6D51A1, 50%);
      border: 0;
      > div {
        display: inline-block;
        margin-right: 10px;
        font-size: 11px;
      }
      .select2-search-choice-close {
        font-size: 8px;
        display: inline-block;
        position: static;
        color: #6D51A1;
        &:before {
          &:extend([class^="icon-"]:before, .icon-remove:before);
        }
      }
      &:hover {
        .select2-search-choice-close {
          color: @white;
          text-decoration: none;
        }
      }
    }
  }

  .select2-choice {
    .form-jumbo.form-control;
    .select2-arrow {
      background: none;
      border: 0;
      &:before {
        &:extend([class^="icon-"]:before, .icon-caret-down:before);
        position: absolute;
        right: 10px;

        // centers vertically because line-height is 1, so 1em
        // is equal to the element's height
        .lh-calc(top, ~'50% - .5em');
      }
      b {
        display: none;
        background: none; // override image url
      }
    }
  }
  &.select2-container-active {
    .select2-choices {
      background: @purple-light;
      &:extend(.form-jumbo .form-control:focus);
    }
    .select2-choice {
      .select2-arrow {
        color: @purple;
      }
      &:extend(.form-jumbo .form-control:focus);
    }
  }

  .select2-input {
    background: transparent;
  }
}
