//
// Forms
// --------------------------------------------------


// TODO lets take this out on one of the next pass -dylan
*:focus {
    outline: 0;
}

label {
	font-weight: 400;
	color: @form-label-color;
	margin-bottom: 2px;
}

input[type="radio"],
input[type="checkbox"] {
  margin: 3px 0 0;
}

.input-group {
	&.addon-inside {
		position: relative;

		.input-group-addon {
			position: absolute;
			top: 6px;
			left: 6px;
			padding: 0;
			background-color: transparent;
			border-color: transparent;
		}

		input[type="text"],
		input[type="password"],
		input[type="email"] {
			padding-left: 30px;
			display: block;
		}
	}
}

.form-group {
	label {
		font-size: 12px;
	}
}

input[type="text"]&,
input[type="password"]&,
input[type="email"]& {
	border-radius: @input-border-radius;
    border-width: @input-border-width;
    .lh-box-shadow(none);
    line-height: 18px;
    &:focus {
        background: @input-bg-focus;
        border-width: @input-border-width;
    }
}

.form-control {
	border-radius: @input-border-radius;
	line-height: 15px;

	&::-webkit-input-placeholder { /* WebKit browsers */
		color:    @gray-dark;
	}

	&:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
	    color:    @gray-dark;
	}

	&::-moz-placeholder { /* Mozilla Firefox 19+ */
	    color:    @gray-dark;
	}

	&:-ms-input-placeholder { /* Internet Explorer 10+ */
	    color:    @gray-dark;
	}
}

input[type="text"],
input[type="password"] {
    outline: none;
}

.no-resize {
    resize: none;
}

.form-group{
    textarea {
        height: 100%;
    }
}

.file-dropzone {
    .lh-transition(opacity 300ms);
    .lh-box-sizing(border-box);
    .lh-opacity(1.0);

    width: 100%;
    padding: 20px;
    min-height: 95px;
    border: 2px dashed rgba(102, 102, 102, 1);
}

.dropping {
    .file-dropzone {
        .lh-transition(all 300ms);
        background: lighten(@green, 50%);
        border-color: @green;
        .lh-opacity(0.6);

        > * {
            visibility: hidden;
        }
    }
}

.form-header {
    margin-bottom: 20px;
}

.form-header.margin-top {
    margin-top: 40px;
}

.form-footer {
    background-color: @form-footer-bg;
    padding: 16px;
    margin: 20px -15px -15px;
}

.form-footer-btn {
    margin-bottom: 20px;
    margin-top: 10px;
}

.form-remove-btn {
    height: 65px;
    margin-top: 16px;
}

.input-group.addon-inside {
    input {
        .lh-border-radius(4px);
    }
}

.input-group-addon:not(:first-child):not(:last-child),
.input-group-btn:not(:first-child):not(:last-child),
.input-group .form-control:not(:first-child):not(:last-child) {
    .lh-border-radius(3px);
}



// code below is potentially going into ads+data bootstrap, so be very careful
.form-jumbo {


    @media (max-width: @screen-sm-max) {
        & .row {
            margin-right: 0;
            margin-left: 0;
            > [class*="col-"] {
                padding-right: 0;
                padding-left: 0;
            }
        }
    }

    @media (min-width: @screen-md-min) {
        & .row > [class*="col-"] {
            padding-left: 0;
            padding-right: 1px;

            &:first-of-type {
                padding-left: (@grid-gutter-width / 2);
            }

            &:last-of-type {
                padding-right: (@grid-gutter-width / 2);
                // fixes icons
                &:before {
                    padding-right: 16px;
                }
            }
        }
    }

    .select2-container-arrow {
        position: relative;
        &:after,&:before {
            right: -19px;
            top: 50%;
            border: solid transparent;
            content: " ";
            height: 0;
            width: 0;
            position: absolute;
            pointer-events: none;
            z-index: 1;
        }
        &:before {
            border-left-color: @gray-light;
            border-width: 30px 10px 30px 10px;
            margin-top: -30px;
        }
        &:after {
            border-left-color: white;
            border-width: 33px 11px 33px 11px;
            margin-top: -33px;
        }
    }

    .select2-container-active {
        &:before {
            border-left-color: @purple;
        }
        &:after {
            border-left-color: @purple-light;
        }
    }

    .select2-container .select2-choice > .select2-chosen {
        margin-left: 0;
    }

    .radio-inline ~ .radio-inline,
    .checkbox-inline ~ .checkbox-inline {
        &:extend(.checkbox-inline + .checkbox-inline);
    }

    input[type="radio"],
    input[type="checkbox"] {
        margin-top: 4px;
    }

    h3 {
        margin-top: 1.5em;
    }

    & > .row > [class*="col-"],
    .form-group {
        margin-bottom: 0;
        min-height: 60px;
        position: relative;
        height: 100%;

        &:before {
            &:extend([class^="icon-"]:before);
            position: absolute;
            right: 13px;
            top: 24px;
        }

        &.align-icon-left {
            .form-control {
                padding: 24px 10px 16px 34px;
            }
            &:before {
                right: auto;
                left: 13px;
            }
        }

        &.form-group-select {
            &:before {
                &:extend(.hidden);
            }
        }

        > label {
            font-size: @input-font-size-label;
            position: absolute;
            z-index: 1; // needed to be above select2
            .lh-translate(10px, 15px);
            color: @gray-dark;
            .lh-transition(all .2s ease-out);
            .transition-property(transform opacity);
            .lh-opacity(0.0);
        }

        .form-control {
            h4 {
                margin: -15px 0 0;
            }
            small {
                font-size: 12px;
            }

        }

        .form-control ~ .btn-group {
            position: absolute;
            right: 20px;
            // figure out how to programatically calculate height of .btn-group and replace 34px
            .lh-calc(top, ~'50% - '(31px / 2));
        }

        &.has-content {
            > label {
                .lh-opacity(1.0);
                .lh-translate(10px, 8px);
            }
        }

        // Warning
        &.has-warning {
            &:before {
                &:extend(.icon-attention:before);
                color: @state-warning-border;
            }
            .form-control,
            .adbs-select2-container .select2-choices, .adbs-select2-container .select2-choice {
                background: @state-warning-bg;
                border-color: @state-warning-border;
            }
        }
        // Error
        &.has-error {
            &:before {
                &:extend(.icon-attention:before);
                color: @state-danger-border;
            }
            .form-control,
            .adbs-select2-container .select2-choices, .adbs-select2-container .select2-choice {
                background: @state-danger-bg;
                border-color: @state-danger-border;
            }
        }

        // Success
        &.has-success {
            &:before {
                &:extend(.icon-check:before);
                color: @state-success-border;
            }
            .form-control,
            .adbs-select2-container .select2-choices, .adbs-select2-container .select2-choice {
                background: @state-success-bg;
                border-color: @state-success-border;
            }
        }

        .select2-container.select2-container-disabled .select2-choice {
            // again with the overrides of select2
            // also it doesn't use placeholder, it has an input with a value
            // which is why color is used
            color: @gray-light !important;
        }

        // bootstrap override?
        &[readonly],
        &[disabled],
        [disabled],
        [readonly],
        fieldset[disabled] & {
            .lh-placeholder(@gray-light);
            color: @gray-light;
        }

    }

    & > .row > [class*="col-"].no-min-height,
    .form-group {
        min-height: inherit;
    }

    .form-control {
        .lh-background-clip(padding-box);
        .lh-box-shadow(none);
        .lh-transition(none);
        font-size: @input-font-size;
        line-height: @line-height-base;
        border-width: @input-border-width;
        border-color: @input-border;

        @media (max-resolution: @projector-max-resolution) and (max-device-width: @projector-max-width) {
          // border-color: darken(@gray-light, 10%) !important;
          border-color: darken(@input-border, 10%);
        }

        margin-bottom: 0;
        background: @input-bg;
        min-height: 60px;
        padding: 24px 34px 16px 10px;

        &:focus {
            background: @input-bg-focus;
            border-color: @input-border-focus;
            .lh-box-shadow(none);
        }

        label {
            font-size: @input-font-size;
            font-weight: @headings-font-weight;
            &.radio-inline,
            &.checkbox-inline {
                font-size: 15px;
            }
        }
    }
}

.btn-group-jumbo {
    .btn {
        .lh-border-radius(0);
        color: #B3B3B3;
        background-image: none;
        background-color: @white;
        border-width: 0 0 1px;
        border-color: @gray-lighter;
        font-size: 13px;
        width: 80px;
        height: 60px;
        margin-right: 2px;

        &:hover
        {
            color: @gray-darker;
        }

        &.active
        {
            background-color: @gray-darker;
            color: @white;

            &:after {
                content: "";
                display: block; /* reduce the damage in FF3.0 */
                position: absolute;
                margin: 8px 22px;
                width: 0;
                border-width: 8px 8px 0;
                border-style: solid;
                border-color: @gray-darker transparent;
            }
        }
    }
}


// TODO - Add styling to non-inline radios & checks
.radio-inline {
  position: relative;
  vertical-align: middle;
  display: inline-block;

  label {
    cursor: pointer;
    height: 16px;
    left: 0;
    padding-left: 5px;
    top: 0;
    border-radius: 100%;

		&:before {
			border: 1px solid @gray-light;
			content: "";
			height: 16px;
			left: 0;
			position: absolute;
			top: 1px;
			width: 16px;
			border-radius: 100%;
		}

		&:after {
			background: @gray-light;
			content: "";
			height: 8px;
			left: 4px;
			position: absolute;
			top: 5px;
			width: 8px;
			opacity: 0;
			border-radius: 100%;
		}
		&:hover,
	  &.hover {
	    color: @purple;

	    &:after {
	      opacity: 1;
	    }
	  }
  }

  input[type="radio"] {
    visibility: hidden;

		&:checked + label {
    	color: @purple;
    }

    &:checked + label:before {
      border: 1px solid @input-border-focus;
    }

    &:checked + label:after {
      background: @purple;
      opacity: 1;
    }

    &[disabled] + label {
    	cursor: not-allowed;
    	color: @input-color-disabled;
    	&:hover:after {
    		opacity: 0;
    	}
    }
  }
}

.checkbox-inline {
  position: relative;
  vertical-align: middle;
  display: inline-block;

  label {
    cursor: pointer;
    height: 15px;
    left: 0;
    padding-left: 5px;
    top: 0;

    &:before {
      border: 1px solid @gray-light;
      content: "";
      cursor: pointer;
      height: 15px;
      left: 0;
      position: absolute;
      top: 1px;
      width: 15px;
      border-radius: 1px;
    }

    &:after {
      background: transparent;
      font-family: @icon-font-name;
      &:extend(.icon-check:before);
      font-size: 9px;
      height: 15px;
      left: 3px;
      position: absolute;
      top: 3px;
      width: 15px;
      opacity: 0;
    }

    &:hover,
    &.hover {
      color: @purple;

      &:after {
      	opacity: 0.5;
      }
    }
  }

  input[type="checkbox"] {
    visibility: hidden;

    &:checked + label {
      color: @purple;
    }

    &:checked + label:before {
      background: @purple;
      border: 1px solid @input-border-focus;
    }

    &:checked + label:after {
      color: @white;
      opacity: 1;
    }

    &[disabled] + label {
    	cursor: not-allowed;
    	color: @input-color-disabled;
    	&:hover:after {
    		opacity: 0;
    	}
    }
  }
}

.table-layout {
    display: table;

    div {
        display: table-cell;
    }
}

.icon-circle-add {
    &.any, &.all, &.not {
        cursor: pointer;
        font-size: 18px;
    }

    &.any {
        color: @blue
    }

    &.all {
        color: @green;
    }

    &.not {
        color: @orange;
    }
}

