}

.form-jumbo-datepicker {
    > .form-control {
        padding-left: 30px;
    }
    > .icon-calendar {
        position: absolute;
        bottom: 15px;
        margin-left: 10px;
    }
}

.make-required() {
    &:after {
        content: " *";
        color: @state-danger-text;
    }
}

.is-required {
    > label,
    // select2
    .select2-default .select2-chosen {
      .make-required();
    }
}
