//
// Labels
// --------------------------------------------------

.label {
	padding: @label-padding;
  color: @gray-darker;
	border: 1px solid @gray;
}

.label-default {
  .label-variant(@label-primary-bg);
}

.label-default,
.label-success,
.label-danger,
.label-info,
.label-warning {
	color: @white;
	border: 0;
}
