// Base styles
// --------------------------------------------------

// Core styles

.btn {
  border-radius: @border-radius-base;
  white-space: nowrap;
  font-weight: 200;
  min-width: 50px;
  &:focus {
    outline: none;
  }
  [class^="icon-"]:before,
  [class*=" icon-"]:before {
    font-size: 90%;
    display: inline-block;
    margin: .2em .15em 0 0;
    line-height: 1.25em;
  }

  i[class*="icon-"] {
		font-size: 10px;
		line-height: 12px;
	}
	i[class*="icon-add"] {
		font-size: 12px;
		margin-right: 5px;
	}
}


// Alternate buttons
// --------------------------------------------------

.btn-default {
  .button-variant(@btn-default-color; @btn-default-bg; @btn-default-border; @btn-default-hover; @btn-default-hover-border; @btn-default-active-color; @btn-default-active-bg);
}
.btn-secondary {
  .button-variant(@btn-secondary-color; @btn-secondary-bg; @btn-secondary-border; @btn-secondary-hover; @btn-secondary-hover-border; @btn-secondary-active-color; @btn-secondary-active-bg);
}
.btn-primary {
  .button-variant(@btn-primary-color; @btn-primary-bg; @btn-primary-border; @btn-primary-hover; @btn-primary-hover-border; @btn-primary-active-color; @btn-primary-active-bg);
}
// TODO - remove color specificity classes
// Warning appears as orange
.btn-warning,.btn-orange {
  .button-variant(@btn-warning-color; @btn-warning-bg; @btn-warning-border; @btn-warning-hover; @btn-warning-hover-border; @btn-warning-active-color; @btn-warning-active-bg);
}
// Danger and error appear as red
.btn-danger,.btn-red {
  .button-variant(@btn-danger-color; @btn-danger-bg; @btn-danger-border; @btn-danger-hover; @btn-danger-hover-border; @btn-danger-active-color; @btn-danger-active-bg);
}
// Success appears as green
.btn-success,.btn-green {
  .button-variant(@btn-success-color; @btn-success-bg; @btn-success-border; @btn-success-hover; @btn-success-hover-border; @btn-success-active-color; @btn-success-active-bg);
}
// Info appears as blue-green
.btn-info,.btn-blue {
  .button-variant(@btn-info-color; @btn-info-bg; @btn-info-border; @btn-info-hover; @btn-info-hover-border; @btn-info-active-color; @btn-info-active-bg);
}

.btn.btn-default {
	border-width: 1px;
}


.btn-blank {
	color: @gray-darker;
	background-color: transparent;

	&:hover,
  &:focus
  {
		color: @gray-darker;
	}

	&:active,
	&.active {
		.box-shadow(none);
	}
}

.btn-flat {
	&.with-border {
		border: 1px solid @gray;
	}
}
